.order-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.order-container .order-btn {
  padding: 7px 20px;
  background-color: rgb(75, 75, 75);
  cursor: pointer;
  border: 3px solid aqua;
  color: aqua;
  border-radius: 5px;
  font-weight: bold;
  text-transform: uppercase;
  margin-bottom: 20px;
}
.order-container .order-btn:hover {
  background-color: rgb(46, 46, 46);
}

.first-container,
.second-container,
.third-container {
  width: 70%;
  color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: rgb(75, 75, 75);
  margin-bottom: 20px;
  border-radius: 15px;
  font-size: 16px;
}

.game-music-header,
.film-music-header,
.theatre-music-header {
  margin-top: 10px;
  text-transform: uppercase;
  font-size: 30px;
  font-family: "Paytone One", sans-serif;
  text-shadow: 2px 2px 2px rgb(0, 0, 0);
}

.offer-container {
  margin-bottom: 20px;
}
.offer-container table {
  border-collapse: collapse;
}
.offer-container table td,
.offer-container table th {
  border: 1px solid rgb(223, 223, 223);
  padding: 10px;
  text-align: center;
}
.offer-container table .empty-cell {
  border: none;
}
.offer-container table th {
  font-weight: bold;
}

@media (max-width: 992px) {
  .first-container,
.second-container,
.third-container {
    font-size: 14px;
    width: 100%;
  }
}
@media (max-width: 700px) {
  .first-container,
.second-container,
.third-container {
    font-size: 10px;
  }
  .offer-container table {
    width: 90%;
    margin: 0 auto;
  }
  .offer-container table td,
.offer-container table th {
    padding: 4px;
  }
}